    <script type="text/javascript">
export default {
  data() {
    return {
      list: [], //奖品列表
      index: 0, // 当前转动到哪个位置，第一次起点位置0,对应页面item1位置
      count: 8, // 总共有多少个位置
      times: 0, // 转动跑格子次数,
      cycle: 60, // 转动基本次数：即至少需要转动多少次再进入抽奖环节
      speed: 200, // 初始转动速度
      lampShow: false, //开始抽奖，灯光闪烁
      timer: 0, // 转动定时器
      lamp: 0, // 灯光定时器
      prize: 0, // 中奖位置，接口返回
      //   number_of_draws: 0, //限制每天抽奖次数，接口返回
      prize_data: {
        //中奖信息
        id: Number, //奖品ID
        name: "", //奖品名称
        number: Number, //奖品数量
        image: "", //奖品图片
        type: Number, // 奖品类型
      },
      popupShow: false, //vant弹出层
    };
  },
  mounted() {
    //获取奖品列表
    // this.axios.post("/api/luckdraw/prizeList").then(res => {
    // if (res.status == 1) {
    this.list = [
      {
        id: 1,
        number: 1,
        prize_name: "麻辣烫",
        image: "./img/炸鸡.png",
      },
      {
        id: 2,
        number: 2,
        prize_name: "鸡公煲",
        image: "./img/炸鸡.png",
      },
      {
        id: 3,
        number: 3,
        prize_name: "水煮鱼",
        image: "./img/炸鸡.png",
      },
      {
        id: 4,
        number: 4,
        prize_name: "华莱士",
        image: "./img/炸鸡.png",
      },
      {
        id: 5,
        number: 5,
        prize_name: "粥",
        image: "./img/炸鸡.png",
      },
      {
        id: 6,
        number: 6,
        prize_name: "小碗菜",
        image: "./img/炸鸡.png",
      },
      {
        id: 7,
        number: 7,
        prize_name: "米饭",
        image: "./img/炸鸡.png",
      },
      {
        id: 8,
        number: 8,
        prize_name: "小吃",
        image: "./img/炸鸡.png",
      },
    ]; // 奖品列表数据
    // this.number_of_draws = 1; // 该用户剩余抽奖次数
    // }
    // });
  },
  methods: {
    //点击开始抽奖
    move() {
      if (this.number_of_draws == 0) {
        this.$toast("今日抽奖次数已用完,明天再来吧");
      } else if (this.times != 0) {
        this.$toast("正在抽奖中，请勿重复点击");
      } else {
        //     // this.axios.post(baseURL + "/api/luckdraw/doDraw").then(res => {
        //     // if (res.status == 1) {
        //     // this.number_of_draws--; //抽奖开始，次数-1
        //     // }
        //     // });
        this.speed = 150; //每次抽奖速度初始化为200
        this.prize_data = {
          id: 1,
          name: "炸鸡",
          number: 1,
          image: "./img/炸鸡.png",
          type: 1,
        };
        this.prize = Math.ceil(Math.random() * 8); //中奖的位置
        console.log(this.prize);
        // this.prize_data = res.data.yes; //已经拿到中奖信息，页面展示，等抽奖结束后，将弹窗弹出
        // this.prize = res.data.yes.id - 1; //中奖位置赋值，跑马灯最终停留位置，这里实际位置需要-1
        this.startRoll(); //执行抽奖
        this.lamp = setInterval(() => {
          //灯光闪烁开启
          this.lampShow = !this.lampShow;
        }, 100);
      }
    },
    // 开始转动
    startRoll() {
      this.times += 1; // 转动次数
      this.oneRoll(); // 转动过程调用的每一次转动方法，这里是第一次调用初始化
      this.usePrize();
    },

    // 每一次转动
    oneRoll() {
      let index = this.index; // 当前转动到哪个位置
      const count = 8; // 总共有多少个位置
      index += 1;
      if (index > count - 1) {
        index = 0;
      }
      this.index = index;
    },

    usePrize() {
      // 如果当前转动次数达到要求 && 目前转到的位置是中奖位置
      if (this.times > this.cycle + 10 && this.prize === this.index) {
        clearTimeout(this.timer); // 清除转动定时器
        clearTimeout(this.lamp); // 清除灯光定时器
        this.lampShow = false; // 白色灯隐藏
        this.times = 0; // 转动跑格子次数初始化为0，可以开始下次抽奖

        if (this.prize_data.type == 0) {
          console.log("未中奖，谢谢参与"); //未中奖提示弹出，
        } else {
          console.log("中奖啦"); //中奖弹出提示
          this.show = true;
        }
      } else {
        if (this.times < this.cycle - 20) {
          this.speed -= 4; // 加快转动速度
        } else {
          this.speed += 10; // 抽奖即将结束，放慢转动速度
        }
        this.timer = setTimeout(this.startRoll, this.speed); //开始转动
      }
    },
  },
};
</script>
